<template>
  <div class="demo-field">
  <h1>Field</h1>
    <div>
    <div class="demo-title demo-first-title"></div>
      <div>
        <VuField v-model="value" :placeholder="''" />
      </div>
    </div>

    <div>
    <div class="demo-title"></div>
      <div>
        <VuField
          v-model="username"
          :label="''"
          icon="clear"
          :placeholder="''"
          required
          @click-icon="username = ''"
        />

        <VuField
          v-model="password"
          type="password"
          :label="''"
          :placeholder="''"
          required
        />
      </div>
    </div>

    <div>
    <div class="demo-title"></div>
      <div>
        <VuField :value="''" :label="''" disabled />
      </div>
    </div>

    <div>
    <div class="demo-title"></div>
      <div>
        <VuField :label="''" :placeholder="''" error />
        <VuField
          v-model="phone"
          :label="''"
          :placeholder="''"
          :error-message="''"
        />
      </div>
    </div>

    <div>
    <div class="demo-title"></div>
      <div>
        <VuField
          v-model="message"
          :label="''"
          type="textarea"
          :placeholder="''"
          rows="1"
          autosize
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  i18n: {
    'zh-CN': {
      title2: '',
      title3: '',
      title4: '',
      title5: '',
      username: '',
      password: '',
      message: '',
      phone: '',
      phonePlaceholder: '',
      usernamePlaceholder: '',
      passwordPlaceholder: '',
      messagePlaceholder: '',
      inputDisabled: '',
      phoneError: ''
    },
    'en-US': {
      title2: 'Custom type',
      title3: 'Disabled',
      title4: 'Error info',
      title5: 'Auto resize',
      username: 'Username',
      password: 'Password',
      message: 'Message',
      phone: 'Phone',
      phonePlaceholder: 'Phone',
      usernamePlaceholder: 'Username',
      passwordPlaceholder: 'Password',
      messagePlaceholder: 'Message',
      inputDisabled: 'Disabled',
      phoneError: 'Invalid phone'
    }
  },

  data() {
    return {
      value: '',
      password: '',
      username: '',
      message: '',
      phone: '1365577'
    };
  }
};
</script>

<style lang="scss">
.demo-field {
  padding: 0 0 30px 0;
  text-align: left;
}
h1{
  margin: 0;
  padding: 15px;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  text-transform: capitalize;
}
.demo-first-title{
  padding-top:0 !important;
}
.demo-title{
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  color: rgba(69,90,100,.6);
  padding: 40px 15px 10px;
}

</style>



