<template>
  <div>
    <h1 class="demo-section__title">dialog</h1>

    <div>
      <h2 class="demo-block__title">{{title1}}</h2>

      <vu-button @click="onClickAlert">Alert</vu-button>
      <vu-button @click="onClickAlert2">{{ alert2 }}</vu-button>
      <vu-button @click="onClickAlert3">{{ alert3 }}</vu-button>
    </div>

    <div>
      <h2 class="demo-block__title">{{title2}}</h2>

      <vu-button @click="onClickConfirm">Confirm</vu-button>
    </div>

    <div>
      <h2 class="demo-block__title">messagehtml</h2>

      <vu-button @click="onClickHtml">/</vu-button>
    </div>
  </div>
</template>

<script>

export default {
  data () {
  	return {
      title1: '',
      title2: '',
      alert2: ' Alert',
      alert3: ' Alert',
      title: '',
      content: '',
      htmlContent: `<span style="display: block; text-align: center"></span>
                   <a style="display: block;  text-align: center; color: #39a9ed;">https://baidu.com</a>`
    }
  },
  methods: {
    onClickAlert() {
      Dialog.alert({
        title: this.title,
        message: this.content
      });
    },

    onClickAlert2() {
      Dialog.alert({
        message: this.content
      });
    },

    onClickAlert3() {
      Dialog.alert({
        title: this.title
      });
    },

    onClickConfirm() {
      Dialog.confirm({
        title: this.title,
        message: this.content
      });
    },

    onClickHtml () {
      Dialog.confirm({
        title: '',
        message: this.htmlContent,
        confirmButtonText: '',
        cancelButtonText: '',
        confirmButtonStyle: {
        	color: '#39a9ed'
        }
      }).then ( () => {
      	alert('')
      })
        .catch (() => {
        alert('')
    });
    }
  }
};
</script>

<style lang="postcss">
  /*.vu-button {*/
    /*margin: 15px;*/
  /*}*/
</style>



